import React, { Component } from 'react';
const _ = require('lodash');

class PointTable extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      row: _.toArray(_.repeat('r', 6)),
    }
    
    this.prevPoint = null
    this.columns = []
  }
  
  genColumnArrayByNumber(number) {
    return _.toArray(_.repeat('c', number))
  }
  
  renderTable() {
    return (
      <div className="row no-gutters">
      
      <div className="col-sm-4 borderHere">
      
      <table className="table table-sm table-responsive table-bordered">
      <tbody>
      {this.renderUnGroupPoints()}
      </tbody>
      </table>
      
      </div>
      
      <div className="col-sm-8">
      
      <table className="table table-sm table-responsive table-bordered">
      <tbody>
      {this.renderGroupPoints()}
      </tbody>
      </table>
      
      </div>
      
      </div>
    )
  }
  
  groupPoints() {
    const pointsOrderByTime = _.orderBy(this.props.points || [], (point) => {
      return point && point.time;
    }, 'asc');
    
    let groupPoints = [];
    let previousPoint = null;
    
    _.forEach(pointsOrderByTime, (point, index) => {
      if ((previousPoint && previousPoint.type !== point.type) && (point.type !== 'Tie')) {
        groupPoints.push([point]);
      } else {
        if (groupPoints.length > 0) {
          let latestRows = [];
          latestRows = groupPoints[groupPoints.length - 1];
          latestRows.push(point);
          if(latestRows.length > 6) {
            const splice = latestRows.splice(6)
            groupPoints.push(splice)
          }
        } else {
          groupPoints.push([point])
        }
      }
      previousPoint = point;
    });
    // console.log('p->', pointsOrderByTime)
    // console.log('r->', groupPoints);
    // console.log('\n')
    return groupPoints
  }
  
  renderPoint(point, col) {
    if (point) {
      return (
        <td key={col}>
        {
          (point.type === 'Player') ? (
            <span className="badge badge-pill badge-primary">{'P'}</span>
          ) :
          (point.type === 'Banker') ? (
            <span className="badge badge-pill badge-danger">{'B'}</span>
          ) : (
            <span className="badge badge-pill badge-success">{'T'}</span>
          )
        }
        </td>
      )
    } else {
      return this.renderEmptyCol(col)
    }
  }
  
  renderEmptyCol(col) {
    return (
      <td key={col}>
      <span className="badge badge-pill">{'x'}</span>
      </td>
    )
  }
  
  renderGroupPoints() {
    const groupPoints = this.groupPoints();
    const column = this.genColumnArrayByNumber(20 + groupPoints.length)
    
    return _.map(this.state.row, (val, row) => {
      return (
        <tr key={row}>
        {
          _.map(column, (val, col) => {
            const pointColumn = groupPoints[col]
            
            if (pointColumn) {
              const point = pointColumn[row]

              return this.renderPoint(point, col)
            } else {
              return this.renderEmptyCol(col)
            }
          })
        }
        </tr>
      )
    })
    
  }
  
  renderUnGroupPoints() {
    const groupPointsByTime = _.orderBy(this.props.points || [], (val) => {
      return val && val.time
    }, 'asc')
    
    const pointsChunk = _.chunk(groupPointsByTime, 6)
    const column = this.genColumnArrayByNumber(9 + pointsChunk.length)
    
    return _.map(this.state.row, (val, row) => {
      return (
        <tr key={row}>
        {
          _.map(column, (val, col) => {
            const pointColumn = pointsChunk[col]

            if (pointColumn) {
              const point = pointColumn[row]

              return this.renderPoint(point, col)
            } else {
              return this.renderEmptyCol(col)
            }            
          })
        }
        </tr>
      )
    })
  }
  
  renderOnBreak() {
    return (
      <div className="d-flex flex-row align-items-center justify-content-center" style={{height: 199}}>
      <div className="p-2">On a Break</div>
      </div>
    )
  }
  
  shouldComponentUpdate(nextProps) {
    return this.props.points.length !== nextProps.points.length
  }
  
  render() {
    return (
      <div>
      
      {
        this.props.onBreak ? this.renderOnBreak() : this.renderTable()
      }
      
      </div>
    )
  }
  
}

export default PointTable


