import React, { Component } from 'react';
import PointTable from './PointTable';

const _ = require('lodash');
const storage = require('store');

const AllPointKeyForTable = 'STORAGE_ALL_POINTS_FOR_TABLE_';

class CardTable extends Component {
  
  constructor(props) {
    super(props);
    
    this.state = {
      playerChange: false,
      bankerChange: false,
      dPlayerChange: false,
      dBankerChange: false,
      tieChange: false,
      totalChange: false,
      
      bankerPoints: [],
      playerPoints: [],
      tiePoints: [],
      allPoints: [], //this.getStoragePoints(props.table),

      onBreak: false,

      testPoints: [], // test points
    }

    // this.clearStorage()
    //this.showAllStore()
    this.runTest = this.runTest.bind(this);
    this.loopCall = this.loopCall.bind(this);
  }

  componentDidMount() {
    //this.runTest()
  }

  runTest() {
   _.delay(this.loopCall, 3000) 
  }

  loopCall() {
    let array = this.randomPoints()
    this.setState({
      testPoints: _.concat(this.state.testPoints, array)
    })
    this.runTest()
  }

  randomPoints() {
    const number = _.random(0, 2)
    const tables = ["A", "B", "C"]
    let point = {
      table: tables[number],
      type: number === 1 ? 'Banker' : number === 2 ? 'Player' : 'Tie',
      value: number,
      time: Date.now()
    }
    return [point]
  }

  clearStorage() {
    storage.clearAll()
  }

  showAllStore() {
    storage.each((key, value) => {
      console.log(key, value)
    })
  }

  getStoragePoints(tableName) {
    const points = storage.get(AllPointKeyForTable + tableName);
    // console.log(points, tableName)
    return points || []
  }
  
  checkStyle(checkCase) {
    let changeColor = { color: '#f00' }
    let normalColor = { color: '#212529' }

    switch (checkCase) {
      case 'Banker':
      return this.state.bankerChange ? changeColor : normalColor
      case 'Player':
      return this.state.playerChange ? changeColor : normalColor
      case 'DPlayer':
      return this.state.dPlayerChange ? changeColor : normalColor
      case 'DBanker':
      return this.state.dBankerChange ? changeColor : normalColor
      case 'Tie':
      return this.state.tieChange ? changeColor : normalColor
      case 'Total':
      return this.state.totalChange ? changeColor : normalColor
      default:
      return normalColor
    }
  }

  componentWillReceiveProps(nextProps) {

    let history = _.get(this.props.data, 'm_RoadHisCnt')
    let nextHistory = _.get(nextProps.data, 'm_RoadHisCnt')

    let _modeChange = this.props.mode !== nextProps.mode

    let _playerChange = _.get(history, 'Player', 0) !== _.get(nextHistory, 'Player', 0)
    let _bankerChange = _.get(history, 'Banker', 0) !== _.get(nextHistory, 'Banker', 0)
    let _dBankerChange = _.get(history, 'DBanker', 0) !== _.get(nextHistory, 'DBanker', 0)
    let _dPlayerChange = _.get(history, 'DPlayer', 0) !== _.get(nextHistory, 'DPlayer', 0)
    let _tieChange = _.get(history, 'Tie', 0) !== _.get(nextHistory, 'Tie', 0)
    let _totalChange = _.get(history, 'HistoryCnt', 0) !== _.get(nextHistory, 'HistoryCnt', 0)

    let _bankerPoint = _.get(nextHistory, 'Banker', 0)
    let _playerPoint = _.get(nextHistory, 'Player', 0)
    let _tiePoint = _.get(nextHistory, 'Tie', 0)
    
    let _onBreak = (_bankerPoint === 0 && _playerPoint === 0 && _tiePoint === 0)
    const _table = _.get(this.props, 'table', 'untitled')

    let point = {
      table: _table,
      type: _bankerChange ? 'Banker' : _playerChange ? 'Player' : _tieChange ? 'Tie' : 'unknown',
      value: _bankerChange ? _bankerPoint : _playerChange ? _playerPoint : _tieChange ? _tiePoint : 0,
      time: Date.now()
    }

    const _pointChange = (_bankerChange || _playerChange || _tieChange)
    const _allPoints = _onBreak || _modeChange ? [] : _pointChange ? _.compact(_.concat(this.state.allPoints, [point])) : _.concat(this.state.allPoints, [])

    this.setState({
      playerChange: _playerChange,
      bankerChange: _bankerChange,
      dBankerChange: _dBankerChange,
      dPlayerChange: _dPlayerChange,
      tieChange: _tieChange,
      totalChange: _totalChange,

      bankerPoints: _bankerChange ? _.compact(_.concat(this.state.bankerPoints, _bankerPoint)) : _.concat(this.state.bankerPoints, []),
      playerPoints: _playerChange ? _.compact(_.concat(this.state.playerPoints, _playerPoint)) : _.concat(this.state.playerPoints, []),
      tiePoints: _tieChange ? _.compact(_.concat(this.state.tiePoints, _tiePoint)) : _.concat(this.state.tiePoints, []),

      allPoints: _allPoints,

      onBreak: _onBreak,
    })

    //console.log('_p', _allPoints)

    // if (_pointChange){
    //   storage.set(AllPointKeyForTable + _table, _allPoints)
    // }

  }
  
  render() {
    let history = _.get(this.props.data, 'm_RoadHisCnt')
    // let mNow = _.get(this.props.data, 'm_now')
    
    //console.log(this.props.data)
    //console.log(this.state.bankerPoints, this.state.playerPoints, this.state.tiePoints)
    //console.log(this.state.allPoints)
    
    return (
      <div className="card">

      <h4 className="card-header">
      {_.get(this.props, 'table', 'Untitled')}
      </h4>

      <div className="card-body">
      
      <table className="table table-striped">
      <tbody>

      <tr>

      <th scope="row">
      <span className="badge badge-pill badge-danger">B</span>
      </th>
      <td style={this.checkStyle('Banker')}>
      {_.get(history, 'Banker', 0)}
      </td>
      <th scope="row">
      <span className="badge badge-pill badge-primary">P</span>
      </th>
      <td style={this.checkStyle('Player')}>
      {_.get(history, 'Player', 0)}
      </td>
      <th scope="row">
      <span className="badge badge-pill badge-success">T</span>
      </th>
      <td style={this.checkStyle('Tie')}>
      {_.get(history, 'Tie', 0)}
      </td>

      </tr>
      
      <tr>

      <th scope="row">DB</th>
      <td style={this.checkStyle('DBanker')}>
      {_.get(history, 'DBanker', 0)}
      </td>
      <th scope="row">DP</th>
      <td style={this.checkStyle('DPlayer')}>
      {_.get(history, 'DPlayer', 0)}
      </td>
      <th scope="row">Total</th>
      <td style={this.checkStyle('Total')}>
      {_.get(history, 'HistoryCnt', 0)}
      </td>

      </tr>

      </tbody>
      </table>
      
      </div>
      
      <div className="card-footer text-muted">

      <PointTable column={this.props.column} points={this.state.allPoints} onBreak={this.state.onBreak} />
      {/* <PointTable points={this.state.testPoints} /> */}


      </div>
      
      </div>
    )
  }
  
}

export default CardTable


